import React from "react";
//
//
import PropTypes from "prop-types";
//
//
import {
  Grid,
  Box,
  Typography,
  AppBar,
  Tabs,
  Tab,
  useMediaQuery,
} from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/core/styles";
//
//
import illustrationTab1 from "../../images/illustration-features-tab-1.svg";

import illustrationTab2 from "../../images/illustration-features-tab-2.svg";
import illustrationTab3 from "../../images/illustration-features-tab-3.svg";
//
//
//
//
const useStyles = makeStyles((theme) => ({
  appBar: {
    padding: 10,
    boxShadow: "none",
  },
  tabsItems: {
    margin: "0 auto",
  },
  tabsItem: {
    textTransform: "none",
    fontSize: ".92rem",
    [theme.breakpoints.up("sm")]: {
      width: 180,
    },
    [theme.breakpoints.down("sm")]: {
      fontSize: ".8rem",
    },
    "&:hover": {
      color: theme.pallete.common.SoftRed,
    },
  },
  indicator: {
    backgroundColor: theme.pallete.common.SoftRed,
    [theme.breakpoints.only("xs")]: {
      backgroundColor: "transparent",
    },
  },
}));
//
//
// nessary attributes for tab
function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}
//
// Tabs component
function Tabz() {
  //
  //
  const classes = useStyles();
  //
  //
  // choosin tab initial state
  const [value, setValue] = React.useState(0);
  // handle tab change
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  //
  //
  // access screen size
  const theme = useTheme();
  const matchesMd = useMediaQuery(theme.breakpoints.up("sm"));
  const matchesXs = useMediaQuery(theme.breakpoints.only("xs"));
  //
  //
  // JSX
  return (
    <div>
      <AppBar position="static" color="transparent" className={classes.appBar}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="simple tabs example"
          className={classes.tabsItems}
          classes={{ indicator: classes.indicator }}
          variant="fullWidth"
          orientation={matchesMd ? "horizontal" : "vertical"}
        >
          <Tab
            label="Simple Bookmarking"
            {...a11yProps(0)}
            className={classes.tabsItem}
            style={{
              borderBottom:
                value === 0 && matchesXs
                  ? `2px solid ${theme.pallete.common.SoftRed}`
                  : `2px solid ${theme.pallete.common.GrayishBlue}`,
            }}
          />
          <Tab
            label="Speedy Searching"
            {...a11yProps(1)}
            className={classes.tabsItem}
            style={{
              borderBottom:
                value === 1 && matchesXs
                  ? `2px solid ${theme.pallete.common.SoftRed}`
                  : `2px solid ${theme.pallete.common.GrayishBlue}`,
            }}
          />
          <Tab
            label="Easy Sharing"
            {...a11yProps(2)}
            className={classes.tabsItem}
            style={{
              borderBottom:
                value === 2 && matchesXs
                  ? `2px solid ${theme.pallete.common.SoftRed}`
                  : `2px solid ${theme.pallete.common.GrayishBlue}`,
            }}
          />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        Item One
      </TabPanel>
      <TabPanel value={value} index={1}>
        Item Two
      </TabPanel>
      <TabPanel value={value} index={2}>
        Item Three
      </TabPanel>
    </div>
  );
}
//
//
export default Tabz;
//
//
//
// Tabpanel (Whats render for each tab)
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}
//
//
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};
